<template>
	<div class="w-full items-center">
		<div class="w-full p-4 bg-white">
			<div class="grid grid-cols-12 gap-2 md:gap-10">
				<!-- cards go here -->
				<div class="card col-span-6 md:col-span-4 hover:shadow-lg relative">
					<div class="p-4 h-230px">
						<p class="font-bold text-xl border-1 border-white border-b-gray-400 pb-1">Product</p>
						<div class="mt-3 font-semibold">
							<p>총 상품 개수 : {{ prdStatisticEntity.totalCnt | thousandFormat }}</p>
							<p class="mt-1">
								등록완료 상품 : {{ prdStatisticEntity.prdRegedCnt | thousandFormat }}
							</p>
							<p class="mt-1">
								미등록 상품 : {{ prdStatisticEntity.prdOnRegCnt | thousandFormat }}
							</p>
						</div>
					</div>
					<div class="p-4 absolute bottom-0 w-full">
						<!-- <span class="font-bold text-sm">UpdateDate</span>
						<span class="block text-gray-500 text-xs">{{ '' | formatDateFull }}</span> -->
						<span class="block md:inline-block text-gray-500 text-xm">UpdateDate : </span>
						<span>{{ prdStatisticEntity.updateDt | formatDateFull }}</span>
					</div>
					<div class="hidden md:inline-block badge right-2 top-4 bg-red-400 text-slate-100">
						<font-awesome-icon icon="fa-solid fa-circle-exclamation" />
						<span class="pl-1">important</span>
					</div>
				</div>
				<div class="card col-span-6 md:col-span-4 hover:shadow-lg relative">
					<div class="p-4">
						<p class="font-bold text-xl border-1 border-white border-b-gray-400 pb-1">Vistor</p>
						<div class="mt-3 font-semibold">
							<p>누적 방문수 : {{ 256031 | thousandFormat }}</p>
							<p class="mt-1">오늘 방문수 : {{ 57 | thousandFormat }}</p>
							<p class="mt-1">어제 방문수 : {{ 126 | thousandFormat }}</p>
						</div>
					</div>
					<div class="p-4 absolute bottom-0 w-full">
						<span class="block md:inline-block text-gray-500 text-xm">UpdateDate : </span>
						<span>{{ new Date() | formatDateFull }}</span>
					</div>
				</div>
				<div class="card col-span-6 md:col-span-4 hover:shadow-lg relative">
					<div class="p-0">
						<GChart
							type="ColumnChart"
							:data="chartData"
							:options="chartOptions"
							:resizeDebounce="500"
						/>
					</div>
					<div class="p-4 absolute bottom-0 w-full mt-2 hidden md:block">
						<span class="block text-gray-500 text-xm"
							>UpdateDate : {{ new Date() | formatDateFull }}</span
						>
					</div>
				</div>
				<div class="card col-span-12 hover:shadow-lg relative">
					<img src="@/assets/img/office.png" alt="" class="w-full opacity-80" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { GChart } from 'vue-google-charts';
import { statisticApi } from '@/api/index.js';

export default {
	name: 'Home',
	components: {
		GChart,
	},
	data() {
		return {
			chartData: [
				['Year', 'Sales', 'Expenses', 'Profit'],
				['2020', 1170, 460, 250],
				['2021', 660, 1120, 300],
				['2022', 1030, 540, 350],
			],
			chartOptions: {
				chart: {
					title: 'Company Performance',
					subtitle: 'Sales, Expenses, and Profit: 2020-2022',
				},
			},
			prdStatisticEntity: {
				selAcntNo: '',
				prdRegedCnt: 0,
				prdOnRegCnt: 0,
				totalCnt: 0,
				selCd01Cnt: 0,
				selCd02Cnt: 0,
				selCd03Cnt: 0,
				selCd04Cnt: 0,
				selCd05Cnt: 0,
				siteCd: '1',
				statisticNo: '',
				createDt: '',
				createNm: '',
				createNo: '',
				updateDt: '',
				updateNm: '',
				updateNo: '',
			},
		};
	},
	methods: {
		getPrdStatistic() {
			statisticApi.getPrdStatisticApi(this.$store.state.selAcntNo).then(response => {
				if (response.data) {
					this.prdStatisticEntity = this.$webUtils.deepCopyObject(response.data);
				}
			});
		},
	},
	mounted() {},
	created() {
		this.getPrdStatistic();
	},
};
</script>

